// :    .
//  : React, Radix Dropdown Menu, useAuth, useEmployeeDialog.
import React from 'react';
import { UserCircleIcon } from '@heroicons/react/24/outline';

import { cn } from '@/lib/utils';
import { useAuth } from '../context/useAuth';
import { Button } from './ui/button';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from './ui/dropdown-menu';
import useEmployeeDialog from '../hooks/useEmployeeDialog';

interface ProfileDropdownProps {
  children?: React.ReactNode;
  triggerClassName?: string;
}

export default function ProfileDropdown({
  children,
  triggerClassName,
}: ProfileDropdownProps) {
  const { user, logout } = useAuth();
  const { open } = useEmployeeDialog();

  if (!user) {
    return null;
  }

  const name =
    user.name || user.telegram_username || user.username || '';

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button
          variant="pill"
          size="pill"
          aria-label=""
          className={cn(triggerClassName)}
        >
          {children ?? <UserCircleIcon className="h-5 w-5" />}
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end" className="min-w-48">
        <DropdownMenuItem
          onSelect={() => {
            if (user.id) {
              open(user.id);
            }
          }}
          className="cursor-pointer"
        >
          <span className="truncate text-accentPrimary">{name}</span>
        </DropdownMenuItem>
        <DropdownMenuSeparator />
        <DropdownMenuItem
          onSelect={() => {
            void logout();
          }}
        >
          
        </DropdownMenuItem>
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
