//       ARCHIVE_ACCESS
// : React Router, useAuth, utils/access
import { type ReactElement } from 'react';
import { Navigate } from 'react-router-dom';
import { useAuth } from '../context/useAuth';
import Loader from './Loader';
import { ARCHIVE_ACCESS, hasAccess } from '../utils/access';

export default function ArchiveRoute({ children }: { children: ReactElement }) {
  const { user, loading } = useAuth();
  if (loading) return <Loader />;
  if (!user) return <Navigate to="/login" />;
  const access = typeof user.access === 'number' ? user.access : 0;
  if (!hasAccess(access, ARCHIVE_ACCESS)) {
    return <Navigate to="/tasks" />;
  }
  return children;
}
